@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Play&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

h1 {
  color: #313131;
  font-size: 2.5rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
}

body {
  font-family: "Roboto", sans-serif;
  background: #fafafa;
  letter-spacing: 1px;
  color: #252525;
}

button {
  background-color: #6e2594;
  letter-spacing: 2px;
  padding: 10px;
  font-family: "Roboto", sans-serif;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  color: #fafafa;
  border: 0;
  width: auto;
  margin: auto;
  border: 2px solid transparent;
  transition: all 0.5s;
  span {
    margin-left: 10px;
  }
  &:hover {
    color: #2c2c2c;
    background: #fafafa;
    border: 2px solid #6e2594;
    cursor: pointer;
  }
}

section {
  width: 100%;
  margin: auto;
}

input {
  border: #6e2594;
  outline: none;
  margin-bottom: 10px;
}

textarea {
  padding: 20px 10px;
  border: 0;
  outline: none;
  margin-bottom: 10px;
  font-family: "Roboto", sans-serif;
}

.site-description {
  text-align: center;
  margin: 20px;
  user-select: none;
  .link {
    color: #fff;
    display: flex;
    align-items: center;
  }
  h4 {
    font-size: 1.8rem;
    font-weight: 700;
  }
  button {
    margin-top: 20px;
    font-size: 1.2rem;
    padding-left: 15px;
  }
}

.link {
  text-decoration: none;
  color: #2c2c2c;
  font-size: 1rem;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  &:hover {
    color: #6e2594;
  }
}

.header {
  background: #fafafa;
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 30px 0 30px 0;
  a {
    text-decoration: none;
  }
}

.shopping_cart {
  min-width: 500px;
  background: #fafafa;
  box-shadow: 0px 5px 10px -5px rgb(0 0 0 / 0.2);
  padding: 20px;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  right: 0;
  top: 0;
  overflow: auto; /* Enable scroll if needed */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100%;
  line-height: 20px;
  gap: 30px;
  .price {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  button {
    margin: 0;
  }
}

.shopping-card-product {
  width: 100%;
  .icon {
    align-self: center;
    margin-left: auto;
  }
  margin: 10px 0 0 0;
  display: flex;
  img {
    width: 100px;
    height: 110px;
  }
  .description {
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.logo {
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: "Pacifico", cursive;
  color: #595959;
  a {
    color: #1a1a1a;
  }
}

.header-icons {
  color: #303030;

  ul {
    display: flex;
    width: auto;
    list-style: none;
    font-size: 1rem;
    li {
      padding: 10px;
      position: relative;
    }
  }
}

.shopping-cart-counter {
  border: 0;
  border-radius: 180px;
  width: 20px;
  height: 20px;
  background: #6e2594;
  color: #fff;
  padding-left: 2px;

  text-align: center;
  position: absolute;
  top: 25px;
  left: 5px;
  font-size: 0.9rem;
}

.nav {
  ul {
    display: flex;
    width: auto;
    list-style: none;
    font-size: 1rem;
    li {
      color: #595959;
      padding: 10px;
      position: relative;
      a {
        text-decoration: none;
        box-shadow: inset 0 0 0 0 #6e2594;
        color: #313131;
        letter-spacing: 3px;
        padding: 10px 15px 10px 15px;
        transition: color 0.3s ease-in-out, box-shadow 0.5s ease-in-out;

        &:hover {
          box-shadow: inset 200px 0 0 0 #6e2594;
          color: #fff;
        }
      }
    }
  }
}

.hero {
  .wrapper {
    width: 80%;
    height: auto;
    margin: auto;
    display: flex;
    justify-content: space-between;
  }
}
.info {
  width: 70%;
  margin: auto;
  display: flex;
  justify-content: space-around;
  padding: 20px;
  .icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    text-align: center;
    h4 {
      margin-top: 20px;
      margin-bottom: 10px;
      font-size: 1.3rem;
    }
  }
}
.welcome-photo {
  border: 0;
  height: auto;
  animation: welcome 1.5s;
  width: 100%;

  img {
    object-fit: fill;
    width: 100%;
    max-height: 40vw;
  }
}

@keyframes welcome {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.products-shelf {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  gap: 25px;
}
.products-showcase {
  width: 100%;
  margin: auto;
  margin-bottom: 50px;
  position: relative;
}
.products-showcase-title {
  margin-top: 50px;
  text-align: center;
  margin-bottom: 30px;
  span {
    cursor: pointer;
  }
}
.products {
  width: 80%;
  margin: auto;
}

.products-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  margin-top: 30px;
  .card {
    margin: 0;
  }
}

.products-menu {
  display: flex;
  font-size: 0.9rem;
  .right {
    width: 100%;
    margin-top: 15px;
    margin-left: 30px;
    img {
      width: 100%;
      height: 300px;
      object-fit: cover;
    }
  }
  .left {
    .filterItem {
      line-height: 23px;
      h2 {
        margin-bottom: 15px;
        margin-top: 15px;
      }
      .inputItem {
        display: flex;
        align-items: center;
        span {
          padding: 5px;
          font-size: 1rem;
        }
        input[type="checkbox"],
        input[type="radio"] {
          appearance: none;
          position: relative;
          background-color: #fff;
          margin: 0 5px 0 0;
          font: inherit;
          color: #303030;
          width: 1.15em;
          height: 1.15em;
          border: 0.15em solid #303030;
          border-radius: 0.15em;
          transform: translateY(-0.075em);
          place-content: center;
        }
        input[type="checkbox"]::before {
          content: "";
          width: 0.65em;
          height: 0.65em;
          position: absolute;
          left: 0;
          top: 0;
        }
        input[type="checkbox"]:checked,
        input[type="radio"]:checked {
          background-color: #6e2594;
          border: 2px solid #6e2594;

          color: #99a1a7;
        }
      }
    }
  }
}
.product {
  background: #fcfcfc;
  box-shadow: 0px 0px 15px rgb(0 0 0 / 0.2);
  padding: 20px;
  display: none;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 25%;
  top: 10%;
  width: 40%; /* Full width */
  height: 80%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.single-product-page {
  padding: 20px;
  margin: auto;
  width: 80%; /* Full width */
  overflow: auto; /* Enable scroll if needed */
  display: flex;
  transition: opacity 1.2s;
  opacity: 1;
  h4 {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 1.4rem;
  }
  img {
    width: 50%;
    height: auto;
  }
  button {
    margin-left: 0;
  }
  p {
    line-height: 25px;
  }
  .closeModal {
    position: absolute;
    top: 0;
    right: 0;
  }
  .product-add-to-card {
    padding: 5px 40px 5px 40px;
    margin-top: 20px;
  }
  .product-quantity {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 10px;
  }
  .add-to-wishlist {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  .images {
    width: 50%;
    display: flex;
    gap: 10px;
    .thumbnails {
      width: 150px;
      height: 150px;
      display: flex;
      flex-direction: column;
      img {
        margin-bottom: 10px;
        cursor: pointer;
        width: 150px;
        height: 150px;
      }
    }
    .mainImg {
      width: 100%;
      height: auto;
      img {
        width: 100%;
      }
    }
  }
}

.single-product-page.active {
  opacity: 1;
}

.product-dimensions {
  line-height: 50px;
  font-size: 1.1rem;
}

.product-details {
  margin-left: 20px;
  width: 30%;
}

.scale-up-center {
  -webkit-animation: scale-up-center 1s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: scale-up-center 1s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

