/**
 * ----------------------------------------
 * animation scale-up-center
 * ----------------------------------------
 */
@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.card {
  width: 270px;
  height: 370px;
  position: relative;
  margin-left: 15px;
  line-height: 25px;

  button {
    margin: auto;
    position: absolute;
    top: 50%;
    left: 40%;
  }
  img {
    width: 100%;
    height: 350px;
    object-fit: cover;
    transition: all 0.4s;
  }
  &:hover img {
    cursor: pointer;
    transform: scale(1.3);
  }
  .card-thumbnail {
    overflow: hidden;
    height: 300px;
  }
}

.card-details {
  width: 100%;
  height: 100%;
  background: #fafafa;
  position: absolute;
  top: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  transition: 0.5s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  button {
    margin: auto;
    position: initial;
  }
  h4 {
    font-size: 1.3rem;
    font-weight: 400;
  }
}

.card-title {
  margin-top: 10px;
  button {
    z-index: 999;
  }
}

.about {
  height: auto;
  padding: 30px 0 0 0;
  font-family: "Roboto", sans-serif;
  line-height: 30px;
  background: #fcfcfc;
  .wrapper {
    width: 60%;
    margin: auto;
  }
  div {
    display: flex;
  }
  p {
    font-size: 1.5rem;
  }
  h1 {
    text-align: center;
    margin: 30px 0 40px 0;
  }
}

.about-image {
  width: 500px;
  height: auto;
  margin-right: 60px;
  img {
    width: 300px;
    border-radius: 180px;
  }
}

.about-description {
  width: auto;
}

.footer {
  font-size: 0.9rem;
  line-height: 20px;
  height: auto;
  font-family: "Open Sans", sans-serif;
  margin-top: 30px;
  background: #f0f0f0;
  .wrapper {
    width: 60%;
    margin: auto;
    display: flex;
    gap: 20px;
    justify-content: space-evenly;

    padding: 10px;
  }
  ul {
    list-style: none;
    width: 40%;

    a {
      color: #000;
      text-decoration: none;
    }
  }
  h4 {
    text-align: center;

    padding-bottom: 10px;
  }
}
//Animation//
.slide {
  opacity: 0;
  transition: opacity 1s;
}
.slide.active {
  opacity: 1;
}
//ANIMATION//
.copyright {
  margin-top: 20px;
}

.contact-details {
  width: 40%;
  text-align: right;
  h4 {
    text-align: center;
  }
  p {
    text-align: left;
  }
}

.scroll-to-top {
  align-self: flex-start;
  position: relative;
  .icon {
    position: absolute;
    top: -30px;
    left: 40px;
    background: #f0f0f0;
    border: 0;
    border-radius: 180px;
  }
}

.contact {
  padding: 30px 0 30px 0;
  margin: auto;
  margin-top: 40px;
  line-height: 30px;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  h1 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 1.8rem;
  }
  .wrapper {
    width: 60%;
    margin: auto;
    display: flex;
    justify-content: center;
    p {
      font-size: 1.1rem;
    }
    form {
      display: flex;
      flex-direction: column;
      min-width: 500px;
      margin-left: 30px;
      button {
        font-size: 1.1rem;
        margin-top: 20px;
      }
      input {
        padding: 20px 10px;
      }
    }
  }
}

.reveal {
  position: relative;
  transition: opacity 0.2s;
  opacity: 0;
}
.reveal.active {
  opacity: 1;
  animation: slide-in-left 2s;
}
.reveal-right {
  position: relative;
  transition: opacity 0.2s;
  opacity: 0;
  &.active {
    opacity: 1;
    animation: slide-in-right 2s;
  }
}

